<template>
  <page-wrapper>
      <v-row class="d-flex align-items-center">
        <v-col md="4">
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="dateRangeText"
                label="Select Date"
                prepend-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="dates" range>
              <v-spacer></v-spacer>
              <v-btn text color="primary" @click="saveDate">OK</v-btn>
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col>
          <v-btn @click="getData">Search</v-btn>
        </v-col>
      </v-row>
      <b-table
        id="my-table"
        :per-page="table.perPage"
        :current-page="table.currentPage"
        responsive
        hover
        :fields="fields"
        :items="items"
      ></b-table>
      <div class="d-flex justify-content-end">
        <b-pagination
          v-model="table.currentPage"
          :total-rows="rows"
          :per-page="table.perPage"
          aria-controls="my-table"
        ></b-pagination>
      </div>
  </page-wrapper>
</template>

<script>
import Vue from "vue";
import PageWrapper from "@/components/common/PageWrapper.vue";
import { TablePlugin, PaginationPlugin } from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import fetchReport from "@/data/fetchReport";

Vue.use(TablePlugin);
Vue.use(PaginationPlugin);

export default {
  title: "Report",
  name: "contact-page",
  components: {
    PageWrapper,
  },
  computed: {
    rows() {
      return this.items.length;
    },
    dateRangeText() {
      return this.dates.join(" ~ ");
    },
  },
  methods: {
    saveDate() {
      if (this.dates.length === 2) {
        this.menu = false;
      }
    },
    getData() {
      fetchReport(this.dates[0], this.dates[1]).then((res) => {
        this.items = res;
      });
    },
  },
  mounted() {
    this.dates = [
      new Date().toISOString().substr(0, 10),
      new Date().toISOString().substr(0, 10),
    ];
  },
  data() {
    return {
      menu: false,
      dates: ["2019-09-10", "2019-09-20"],
      table: {
        perPage: 10,
        currentPage: 1,
      },
      range: {
        start: new Date(),
        end: new Date(),
      },
      fields: [
        {
          key: "voltage",
          label: "Voltage (V)",
          sortable: true,
        },
        {
          key: "current",
          label: "Current (A)",
          sortable: true,
        },
        {
          key: "active_power",
          label: "Active Power (W)",
          sortable: true,
        },
        {
          key: "reactive_power",
          label: "Reactive Power (VA)",
          sortable: true,
        },
        {
          key: "apparent_power",
          label: "Apparent Power (VAr)",
          sortable: true,
        },
        {
          key: "frequency",
          label: "Frequency (Hz)",
          sortable: true,
        },
        {
          key: "power_factor",
          label: "PF",
          sortable: true,
        },
        {
          key: "created_on",
          label: "Time",
          sortable: true,
          formatter: (value) => {
            const date = new Date(value);
            return `${date.getDate()}/${date.getMonth()}/${date.getFullYear()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
          },
        },
      ],
      items: [],
    };
  },
};
</script>
