import Axios from "axios";

const defaultMin = {
  min: {
    voltage: {
      data: 0,
      date: null,
    },
    current: {
      data: 0,
      date: null,
    },
    activePower: {
      data: 0,
      date: null,
    },
    reactivePower: {
      data: 0,
      date: null,
    },
    apparentPower: {
      data: 0,
      date: null,
    },
    powerFactor: {
      data: 0,
      date: null,
    },
    frequency: {
      data: 0,
      date: null,
    },
  },
};
const defaultMax = {
  max: {
    voltage: {
      data: 0,
      date: null,
    },
    current: {
      data: 0,
      date: null,
    },
    activePower: {
      data: 0,
      date: null,
    },
    reactivePower: {
      data: 0,
      date: null,
    },
    apparentPower: {
      data: 0,
      date: null,
    },
    powerFactor: {
      data: 0,
      date: null,
    },
    frequency: {
      data: 0,
      date: null,
    },
  },
};

export const getMin = async (device, startDate, endDate) => {
  const url = "http://localhost:8088/getMin";
  const result = await Axios.post(url, {
    device,
    startDate,
    endDate,
  });
  if(result.status === 202){
      return defaultMin.min;
  }
  return result.data.min;
};

export const getMax = async (device, startDate, endDate) => {
  const url = "http://localhost:8088/getMax";
  const result = await Axios.post(url, {
    device,
    startDate,
    endDate,
  });
  if(result.status === 202){
      return defaultMax.max;
  }
  return result.data.max;
};
