<template>
  <page-wrapper>
      <v-container>
        <v-row>
          <h4>Control</h4>
        </v-row>
        <v-row>
          <v-col v-for="device in devices" :key="device.devId" sm="6" md="3" @click.stop="turn(`${device.devId}`)">
            <v-row  class="border ma-1">
            <v-col v-if="device.type == 'lamp'" sm="6">
                <v-img v-if="device.enable" src="@/assets/lamp.png" width="100" height="100" contain/>
                <v-img v-else src="@/assets/lamp_off.png" width="100" height="100" contain/>
            </v-col>
            <v-col v-else sm="6">
                <v-img v-if="device.enable" src="@/assets/fan_on.gif" width="100" height="100" contain/>
                <v-img v-else src="@/assets/fan_off.png" width="100" height="100" contain/>
            </v-col>
            <v-col sm="6">
              <v-row>
                {{device.name}}
              </v-row>
              <v-row>
                <v-col>
                  <v-switch disabled v-model="device.enable" input-value="true"></v-switch>
                </v-col>
              </v-row>
            </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-container>
  </page-wrapper>
</template>

<script>
import PageWrapper from "@/components/common/PageWrapper";
export default {
  components: {
    PageWrapper
  },
  mounted() {
    this.$mqtt.subscribe('gsd/control');
  },
  methods: {
    turn(val){
      console.log(val)
      const a = this.devices.filter((e)=>e.devId === val)
      const json = JSON.stringify({
        device: val,
        value: !a[0].enable,
        address: a[0].address,
        unitId: a[0].unitId
      })
      this.$mqtt.publish('gsd/control', json)
    }
  },
  mqtt: {
    "gsd/control" (data) {
      const message = data.toString();
      const json = JSON.parse(message);
      console.log(json)
      const index = this.devices.findIndex((e)=> e.devId === json.device)
      if( index !== -1 ){
        this.devices[index].enable = json.value;
      }
    },
  },
  data: () => {
    return {
      devices: [
        {
          name: 'Fan 1',
          devId: 'lamp5',
          address: 0,
          unitId: 9,
          type: 'fan',
          enable: false,
          lastUpdate: 'now'
        },
        {
          name: 'Lampu 2',
          devId: 'lamp2',
          type: 'lamp',
          address: 1,
          unitId: 9,
          enable: true,
          lastUpdate: 'now'
        },
      ]
    }
  }
}
</script>

