<template>
    <page-wrapper>
        <v-switch @change="switchChange" v-model="switch1" class="mx-2" label="Relay 1"></v-switch>
        {{ text }}
        <v-row>
            <v-col sm="6">
                {{temp}} C
            </v-col>
            <v-col sm="6">
                {{humid}} %
            </v-col>
        </v-row>
        <v-row class="ml-4">
            <line-chart :chart-data="datacollection" :height="200" :options="options"></line-chart>
        </v-row>
    </page-wrapper>
    
</template>

<script>
import mqtt from 'mqtt';
import PageWrapper from '@/components/common/PageWrapper.vue';
import LineChart from '@/components/graph/Chart';

const mqttOptions = {
      host: 'mqtt://mqtt.kwaiks.me',
      port: 8080,
      clientId: 'mqttjs01',
      username: 'iot',
      password: 'iot2020',
      clean: true,
      rejectUnauthorized: false,
};

const client = mqtt.connect('mqtt://mqtt.kwaiks.me', mqttOptions);

export default {
    name: 'Detail',
    components: {
        LineChart,
        PageWrapper,
    },
    data() {
        return {
            switch1: false,
            text: null,
            temp: 0,
            humid: 0,
            datacollection: null,
            datacollTemp: null,
            options: {
                maintainAspectRatio: false,
                legend: {
                    display: false,
                },
            },
        }
    },
    methods: {
        switchChange() {
            this.text = this.switch1;
            console.log(this.temp);
            client.publish('gsd/relay',this.switch1.toString(), function(err){
                if(err){
                    console.log(err)
                }else{
                    console.log('oka')
                }
            });
        },
        insertData(temp) {
            const now = new Date();
            if(this.datacollTemp !== null){
                this.datacollTemp.label.push(now.toString())
                this.datacollTemp.datasets[0].data.push(temp)
                this.datacollection = this.datacollTemp;
            }else{
                this.datacollTemp = {
                    label: [now.toString()],
                    datasets: [
                        {
                            data: [temp],
                            fill: false,
                            borderColor: '#d32f2f',
                        }
                    ]
                }
                this.datacollection = this.datacollTemp
            }
        }
    },
    mounted() {
        this.datacollTemp = {
                    label: ['a','b'],
                    datasets: [
                        {
                            data: [20.0,20.1],
                            fill: false,
                            borderColor: '#d32f2f',
                        }
                    ]
                }
                this.datacollection = this.datacollTemp
        client.on('connect', () => {
          client.subscribe(`gsd/temp`, (err) => {
              if(err){
                  console.log(err)
              }else{
                  console.log('ok')
              }
          });
        });

        client.on('message', (topic, message) => {
            console.info(topic);
            console.log(message.toString());
            const mes = message.toString();
            const json = JSON.parse(mes);
            this.temp = json.temp;
            this.humid = json.humid;
            this.insertData(json.temp);
        });

        client.on('error', () => {
          client.end();
        });
    },
    beforeDestroy() {
        client.end();
    },
}
</script>