import Vue from 'vue';
import Router from 'vue-router';
import store from '@/store';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: require('@/pages/DashboardPage').default,
      meta: {
        title: 'Dashboard'
      }
    },
    {
      path: '/report',
      name: 'contact-page',
      component: require('@/pages/ReportingPage').default,
    },
    {
      path: '/detail',
      name: 'detail-page',
      component: require('@/pages/DetailPage').default,
    },
    {
      path: '/login',
      name: 'login-page',
      component: require('@/pages/LoginPage').default,
    },
    {
      path: '/control',
      name: 'control-page',
      component: require('@/pages/ControlPage').default
    }
  ]
})

router.beforeEach((to,from,next)=>{
  const pageName = to.name;
  const authenticated = store.getters.authenticated;
  console.log(authenticated)
  if(!authenticated && pageName !== 'login-page'){
    return next({path:'/login'})
  }else if(authenticated && pageName === 'login-page'){
    return next({path:'/'})
  }
  return next();
})

export default router;