<template>
    <v-container class="fill-height">
        <div class="d-flex justify-center align-center" style="height:100%;width:100%">
            <v-col sm="12" md="5" class="py-5 border rounded justify-space-around align-center d-flex flex-column" style="height:60vh">
                <v-img src="@/assets/neutronlogo.png" max-height="50px" contain></v-img>
                <h2>LOGIN</h2>
                <div class="d-flex flex-column pa-5">
                    <v-text-field
                        v-model="username"
                        outlined
                        color="primary"
                        label="Username"
                        
                    >
                    <v-icon slot="prepend-inner" color="primary">
                        mdi-account
                    </v-icon>
                    </v-text-field>
                    <v-text-field
                        v-model="password"
                        outlined
                        color="primary"
                        label="Password"
                        type="password"
                        
                    >
                    <v-icon slot="prepend-inner" color="primary">
                        mdi-key
                    </v-icon>
                    </v-text-field>
                </div>
                <div>
                    <v-btn @click="login">Click</v-btn>
                </div>
            </v-col>
        </div>
        </v-container>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
    name: 'login-page',
    title: 'Login',
    methods: {
        ...mapActions(['authenticate']),
        login () {
            this.authenticate({username: this.username, password: this.password})
        },
    },
    data() {
        return {
            username: '',
            password: ''
        }
    },
    computed: {
        ...mapGetters(['authenticated'])
    },
    // mounted() {
    //     const topic = `Neutron/${this.coba}/#`;
    //     this.$mqtt.subscribe(topic)
    // },
}
</script>

