<script>
import PageWrapper from "@/components/common/PageWrapper";
import LineChart from "@/components/graph/Chart";
//import fetchGraph from "@/data/fetchGraphChart";
import ContentTitle from "@/components/ContentTitle.vue";
import ContentItem from "@/components/ContentItem.vue";
import ContentPhase from "@/components/ContentPhase.vue";
import convertDate from "@/common/ConvertDateTime";
import { getMin, getMax } from "@/data/fetchMinMax";

export default {
  title: "Dashboard",
  components: {
    LineChart,
    PageWrapper,
    ContentTitle,
    ContentItem,
    ContentPhase,
  },
  methods: {
    async fetchData() {
      getMin("lantai 8", this.dates[0], this.dates[1]).then((res) => {
        this.min = res;
      });
      getMax("lantai 8", this.dates[0], this.dates[1]).then((res) => {
        this.max = res;
      });
    },
    saveDate() {
      if (this.dates.length === 2) {
        this.menu = false;
      }
    },
    toDate(date) {
      if (date === null) {
        return "";
      }
      return convertDate(date);
    },
    unsub(){
      this.$mqtt.unsubscribe('Neutron/kwh1/#');
    }
  },
  data() {
    return {
      meter: {
        l1: {
          voltage: 0,
          current: 0,
          activePower: 0,
          reactivePower: 0,
          apparentPower: 0,
          powerFactor: 0.0,
          frequency: 0,
        },
        l2: {
          voltage: 0,
          current: 0,
          activePower: 0,
          reactivePower: 0,
          apparentPower: 0,
          powerFactor: 0.0,
          frequency: 0,
        },
        l3: {
          voltage: 0,
          current: 0,
          activePower: 0,
          reactivePower: 0,
          apparentPower: 0,
          powerFactor: 0.0,
          frequency: 0,
        },
      },
      min: {
        voltage: {
          data: 0,
          date: "2020-07-24T12:28:02.02089+00:00",
        },
        current: {
          data: 0,
          date: null,
        },
        activePower: {
          data: 0,
          date: null,
        },
        reactivePower: {
          data: 0,
          date: null,
        },
        apparentPower: {
          data: 0,
          date: null,
        },
        powerFactor: {
          data: 0,
          date: null,
        },
        frequency: {
          data: 0,
          date: null,
        },
      },
      max: {
        voltage: {
          data: 0,
          date: null,
        },
        current: {
          data: 0,
          date: null,
        },
        activePower: {
          data: 0,
          date: null,
        },
        reactivePower: {
          data: 0,
          date: null,
        },
        apparentPower: {
          data: 0,
          date: null,
        },
        powerFactor: {
          data: 0,
          date: null,
        },
        frequency: {
          data: 0,
          date: null,
        },
      },
      totalEnergy: 0,
      energyBill: "Rp 0",
      device: "pm5500",
      selection: "energy",
      floor: "lantai 8",
      datacollection: null,
      timer: null,
      options: {
        maintainAspectRatio: false,
        legend: {
          display: false,
        },
      },
      dates: ["2019-09-10", "2019-09-20"],
      range: {
        start: new Date(),
        end: new Date(),
      },
      menu: false,
    };
  },
  mounted() {
    const topic = `iot/pm1`;
    this.$mqtt.subscribe(topic);
    const date = new Date();
    this.dates = [
      `${date.getFullYear()}-${
        date.getMonth() - 1 > 10
          ? date.getMonth() - 1
          : "0" + (date.getMonth() - 1)
      }-${date.getDate() > 10 ? date.getDate() : "0" + date.getDate()}`,
      date.toISOString().substr(0, 10),
    ];
    this.fetchData();
  },
  computed: {
    dateRangeText() {
      return this.dates.join(" ~ ");
    },
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  mqtt: {
    "iot/pm1"(data) {
      const message = data.toString();
      const json = JSON.parse(message);
      this.meter.l1 = json.kwh2;
    },
  },
};
</script>
